.controls {
  padding: 5px;
  &__block {
    display: flex;
    margin-top: 10px;
    justify-content: flex-start;
    align-items: stretch;
    width: 350px;
  }
  &__btn-block {
    display: flex;
    margin-top: 10px;
    justify-content: flex-start;
    align-items: stretch;
    width: 350px;
  }
  &__input {

  }
  &__color {
  
  }

  &__btn {
    cursor: pointer;
    flex: 1 1 auto;
    border-radius: 3px;
    border: 1px solid #000;
    box-shadow: 3px 3px;
    background-color: var(--btn-color);
    transition: box-shadow 300ms ease, transform 500ms ease;
    &_blue{
    background-color: var(--another-btn-color);

    }
    &:hover {
      box-shadow: 2px 2px;
    }
    &:active {
      box-shadow: none;
      transform: scale(0.9);
    }
    &:disabled {
      box-shadow: none;
      background-color: rgb(167, 166, 166);
    }
  }
  
}
