@font-face {
  font-family: 'icons';
  font-weight: normal;
  font-style: normal;
  font-display: block;
  src: url('../fonts/icons.woff') format('woff'), url('../fonts/icons.ttf') format('ttf');
}

%ic {
  font-family: 'icons';
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
}

[class^='_icon-'],
[class*='_icon-'] {
  &:before {
    @extend %ic;
    display: block;
  }
}

._icon-cart-1:before {
  content: '\e904';
}
._icon-cart-2:before {
  content: '\e905';
}
._icon-cart-3:before {
  content: '\e900';
}
._icon-cart-4:before {
  content: '\e901';
}
._icon-cart-5:before {
  content: '\e902';
}
._icon-cart-6:before {
  content: '\e903';
}
