.car {
  padding: 5px;
  &__select,
  &__remove,
  &__a,
  &__b {
    cursor: pointer;
    border-radius: 3px;
    border: 1px solid #000;
    box-shadow: 3px 3px;
    transition: box-shadow 300ms ease, transform 500ms ease;
    &:hover {
      box-shadow: 2px 2px;
    }
    &:active {
      box-shadow: none;
      transform: scale(0.9);
    }
    &:disabled {
      box-shadow: none;
      background-color: rgb(167, 166, 166);
    }
  }

  &__select,
  &__remove {
    background-color: var(--btn-color);
  }
  &__a {
    background-color: var(--another-btn-color);
  }
  &__b {
    background-color: #eb7e7e;
  }

  &__name {
    display: inline-block;
    margin-left: 10px;
    font-size: 1.1rem;
    color: var(--text-color);
    font-weight: bold;
  }
  &__track {
    display: flex;
  }
  &__road {
    margin-top: 20px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex: 1 1 auto;
    border-bottom: 3px dotted rgb(255, 255, 255);
  }
  &__car {
    position: relative;
    font-size: 1.7rem;
    color: rgb(34, 216, 34);
  }
  &__finish {
    position: relative;
    right: 100px;
    height: 30px;
  }

  &__smog {
    display: none;
    &._active {
      display: inline-block;
      height: 98px;
      top: -288%;
      right: -16px;
      position: absolute;
      z-index: 0;
    }
  }
}

.form-4__button:last-child {
  border: none;
  padding-bottom: 0;
}
