.garage {
  &__pagination{
    bottom: 0;
    padding: 5px;
    position: fixed;
    background: #000;
    border-radius: 5px;
  }
  &__pagination-prev,
  &__pagination-next {
    cursor: pointer;
    border-radius: 3px;
    border: 1px solid #000;
    box-shadow: 3px 3px;
    background-color: var(--btn-color);
    transition: box-shadow 300ms ease, transform 500ms ease;
    &:hover {
      box-shadow: 2px 2px;
    }
    &:active {
      box-shadow: none;
      transform: scale(0.9);
    }
  }
}
